.meteo-block {
    border: 1px solid #ccc;
    padding: 1em;
    background: #eef;
    font-family: sans-serif;
}

/* CSS */
.loader {
    width: 60px;
    height: 60px;
    border: 6px solid #e0e0e0;
    border-top: 6px solid #0073aa; /* Couleur personnalisable */
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin: auto;
  }
  
  @keyframes spin {
    0%   { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
  
